<template>
  <section>
    <header class="header flex space-between align-center" v-show="!isOpen">
      <img :src="logoSrc" alt="" width="79" height="18" />
      <img :src="tabSrc" alt="" width="16" height="14" @click="open" />
    </header>
    <transition name="fade">
      <left-component v-show="isOpen" @close="close"></left-component>
    </transition>
  </section>
</template>

<script>
import LeftComponent from "@/components/AppLeftComponent.vue";

export default {
  name: "AppHeaderComponent",
  components: { LeftComponent },
  data() {
    return {
      logoSrc: require("@/assets/img/app/logo.png"),
      tabSrc: require("@/assets/img/app/tab.svg"),
      isOpen: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    open() {
      this.isOpen = true;
    },
    close(e) {
      this.isOpen = e;
    },
    tabClick(e) {
      // this.$store.state.pageTabId = e.target.dataset.id;
      e.target.dataset.url && this.$router.push(e.target.dataset.url);
    },
    handleScroll() {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.isFixed = scrollTop > 48
    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  }
};
</script>

