<template>
  <section class="tab-page">
    <header class="header flex space-between align-center">
      <img :src="logoSrc" alt="" width="79" height="18" />
      <img :src="closeSrc" alt="" width="32" height="32" @click="close" />
    </header>
    <ul @click="tabClick" class="tabList">
      <li v-for="tab in tabList" :key="tab.id" :data-id="tab.id" :data-url="tab.url">
        {{ tab.title }}
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  name: "AppLeftComponent",
  props: {
    msg: String,
  },
  data() {
    return {
      logoSrc: require("@/assets/img/app/logo.png"),
      closeSrc: require("@/assets/img/app/close.svg"),
      tabList: [
        { id: 1, title: "首页", url: "/app" },
        { id: 2, title: "NIUNIU故事", url: "/app#story" },
        { id: 3, title: "全明星计划", url: "/app#about" },
        { id: 4, title: "产品下载", url: "/app/product" },
        { id: 5, title: "人才招聘", url: "/app/job" },
      ],
    };
  },
  mounted() {
    // window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    close() {
      this.$emit('close', false)
    },
    tabClick(e) {
      // this.$store.state.pageTabId = e.target.dataset.id;
      e.target.dataset.url && this.$router.push(e.target.dataset.url);
      this.$emit('close', false)
    },
    handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.isFixed = scrollTop > 48;
    },
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

